import React, { useRef } from 'react';
import emailjs from '@emailjs/browser';

export const ContactUs = () => {
  const form = useRef();

  const sendEmail = (e) => {
    e.preventDefault();

    emailjs
      .sendForm('service_uymmsyi', 'template_0dkayqe', form.current, {
        publicKey: 'lhFspnTY304sz37gf',
      })
      .then(
        () => {
          console.log('SUCCESS!');
        },
        (error) => {
          console.log('FAILED...', error.text);
        },
      );
  };

  return (
    <form ref={form} onSubmit={sendEmail} className='form'>
        <div className='fields'>
        <label className='nameString' for="subject">Subject</label>
        <input type="text" name="subject" id="subject" placeholder="Let's talk"/>
        </div>
      <div className='fields'>
        <label className='nameString' for="from_name">From:</label>
        <input type="text" name="from_name" id="from_name" placeholder="Jane"/>
      </div>
      
      <label className='nameString' for="Date">Appointment Date </label>
      <input  type="text" name="date" id="message"  />

      <label className='nameString' for="message">Message</label>
      <input className='message' type="text" name="message" id="message"  />

    
      <label className='nameString' for="from_number">Contact Number</label>
      <input type="text" name="from_number" id="from_number" placeholder="+1 (786) 633-5515"/>

      <label className='nameString' for="reply_to">Email:</label>
      <input type="text" name="reply_to" id="reply_to" placeholder="jane@acme.com"/>

      <button className='formButton' type="submit">Submit</button>
    </form>
  );
};

export default ContactUs;